*{
    margin: 0;
    padding: 0;
}

body{
background-image: url(bg.jpg);
background-repeat: no-repeat;
background-size: cover;
display: flex;
align-items: center;
justify-content: center;
height: 100vh;
font-family: sans-serif;
}

.title{
    font-size: 35px;
text-align: center;
color: rgb(10, 106, 18);
}

.app{
background: linear-gradient(83deg, #6fba46 5%, #145213 70% );
display: flex;
justify-content: space-evenly;
border-radius: 20px;
padding: 20px;
height: min-content;
min-height: 200px;
width: 90%;
box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
}
.question-section{
    position: relative;
    width: 100%;
    color: white;
}
.question-section h1 span{
    color: rgba(236, 48, 19, 0.955);
    font-size: 35px;
}
.question-text{
font-size: 25px;
color: white;
margin-top: 15px;
}
.answer-section{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 100%;
}
button{
    color: white;
    background-color: rgb(42, 100, 42);
    cursor: pointer;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 15px;
    border: 4px solid rgb(16, 50, 14);
    border-radius: 8px;

}
.score-section{
    display: flex;
    align-items: center;
    text-align: center;
    color: white;
    font-size: 35px;
    font-weight: bolder;
}